:root {
  --main-font: "Roboto", sans-serif;
}

*, *:before, *:after {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
  -webkit-tap-highlight-color: transparent !important;
}

html, body, div, span, object, iframe, figure, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, code, em, img, small, strike, strong, sub, sup, tt, b, u, i, ol, ul, li, fieldset, form, label, table, caption, tbody, tfoot, thead, tr, th, td, main, canvas, embed, footer, header, nav, section, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  text-size-adjust: none;
}

footer, header, nav, section, main {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a, span {
  text-decoration: none;
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

input, button, textarea {
  background-color: transparent;
  border: none;
  border-radius: 0;
}

div, input, button, textarea {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
}
div:focus, div:focus-visible, input:focus, input:focus-visible, button:focus, button:focus-visible, textarea:focus, textarea:focus-visible {
  outline: none;
}
div:focus::placeholder, input:focus::placeholder, button:focus::placeholder, textarea:focus::placeholder {
  color: transparent;
}

input, textarea {
  -webkit-appearance: none;
  appearance: none;
}

textarea {
  resize: none;
}

button {
  padding: 0;
}

input::-ms-clear {
  display: none;
  width: 0;
  height: 0;
}
input::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}
input[type=search]::-webkit-search-decoration, input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-results-button, input[type=search]::-webkit-search-results-decoratio {
  display: none;
}
input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
  display: none;
  -webkit-appearance: none;
  margin: 0;
}
input[type=number] {
  -moz-appearance: textfield;
}
input:-webkit-autofill, input:-webkit-autofill:focus {
  box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0), inset 0 0 0 100px rgb(255, 255, 255);
}

.navbar {
  display: flex;
  align-items: center;
  width: 100%;
  max-height: 100px;
  padding: 30px;
  background-color: #7FAFF6;
}

.footer__inner {
  padding-top: 100px;
}
.footer__block {
  width: 100%;
  height: 100px;
  background-color: #7FAFF6;
}

.block-first__inner {
  padding-top: 15px;
}
.block-first__body {
  display: flex;
  flex-wrap: wrap;
  background-color: #B3F95A;
}
.block-first__item {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 198px;
  background-color: #8AD191;
}
.block-first__item::before {
  font: 400 5.3rem/6.2rem var(--main-font);
  color: #FFFFFF;
}
.block-first__item:nth-child(-n+3) {
  margin-bottom: 15px;
}
.block-first__item:nth-of-type(1) {
  flex-basis: 33.33%;
  margin-right: 1.28%;
}
.block-first__item:nth-of-type(1)::before {
  content: "1";
}
.block-first__item:nth-of-type(2) {
  flex-basis: 50.5%;
  margin-right: 1.28%;
}
.block-first__item:nth-of-type(2)::before {
  content: "2";
}
.block-first__item:nth-of-type(3) {
  flex-basis: 13.61%;
}
.block-first__item:nth-of-type(3)::before {
  content: "3";
}
.block-first__item:nth-of-type(4) {
  flex-basis: 85.11%;
  margin-right: 1.28%;
}
.block-first__item:nth-of-type(4)::before {
  content: "4";
}
.block-first__item:nth-of-type(5) {
  flex-basis: 13.61%;
}
.block-first__item:nth-of-type(5)::before {
  content: "5";
}

.block-second__inner {
  padding-top: 15px;
}
.block-second__body {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px;
  background-color: #88B5BF;
}
.block-second__item-container {
  width: 25%;
}
.block-second__item-container:not(:last-of-type) {
  margin-right: 30px;
}
.block-second__item-block {
  width: 100%;
  padding-bottom: 78.04%;
  background-color: #0D89B0;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px 15px;
  border-radius: 5px;
  background-color: #A5C7F9;
  font: normal 400 2rem/2.3rem var(--main-font);
  color: #FFFFFF;
  transition: all 0.2s;
  cursor: pointer;
}
.btn:hover {
  background-color: #407EDA;
}
.btn:focus-visible {
  background-color: #407EDA;
}

.popup {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  visibility: hidden;
  opacity: 0;
  transition: all 0.2s;
  z-index: 1;
}
.popup:target {
  visibility: visible;
  opacity: 1;
}
.popup__body {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: min(90%, 400px);
  height: 400px;
  margin: 10% auto 0;
  padding: 10px;
  background-color: #FFFFFF;
}
.popup__content {
  text-align: center;
  font: 400 5.3rem/6.2rem var(--main-font);
  color: #000000;
}
.popup__backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  cursor: pointer;
}
.popup__close {
  position: absolute;
  top: 15px;
  right: 15px;
}

html, body {
  width: 100%;
  height: 100%;
}

html {
  font-size: 62.5%;
}

body {
  display: flex;
  flex-direction: column;
}

.inner {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  background-color: #C0DBD9;
}